.logo {
  text-align: center;
}

.logo a {
  color: #fff;
  display: inline-block;
  padding: 5px;
}

.logo a svg {
  display: block;
  fill: currentColor;
}

.play-ground .code-expand-icon {
  margin-top: 10px;
  display: inline-block;
  cursor: pointer;
  width: 16px;
  height: 16px;
  line-height: 16px;
  text-align: center;
}
.play-ground .code-expand-icon img {
  max-width: 100%;
  width: 100%;
}

.CodeMirror.cm-s-solarized.cm-s-light {
  margin-top: 15px;
  border-top: 1px dashed #ebedf0;
  background-color: inherit !important;
  box-shadow: none !important;
  height: auto;
}

.ant-layout-sider-zero-width-trigger {
  z-index: 1000;
}
